/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */
 $colors: (
	"red": #FF5733,
	"blue": #33D1FF,
	"green": #33FFBD,
	"yellow": #FFFB33,
	"purple": #B633FF
  );
  
  #abel-wrapper {
	font-size: 0; // Remove whitespace between elements
	display: grid; // Use Grid layout
	grid-template-columns: repeat(auto-fit, minmax(100px, 1fr)); // Responsive columns
	grid-gap: 0; // Set the grid gap
	overflow: hidden; // Hide the overflow
	width: 100%;
    height: 75vh;
  }
  
  .shape {
  display: inline-block;
  text-align: center;
  cursor: pointer;
  transition: background-color 1s ease, border-color 1s ease, width 1s ease, height 1s ease, border-radius 1s ease;
  box-sizing: border-box; // Include padding and border in the dimensions
  margin: 0; // Remove margin
}
  
  @keyframes shapeChange {
	0%, 100% {
	  border-radius: 0%;
	}
	25% {
	  border-radius: 50%;
	}
	50% {
	  border-radius: 0%;
	}
	75% {
	  border-radius: 50%;
	}
  }
  
  @media screen and (max-width: 480px) {
	.shape {
	  font-size: 0.8em;
	}
  }
  .triangle {
	clip-path: polygon(50% 0, 0% 100%, 100% 100%);
  }
  
  .pentagon {
	clip-path: polygon(50% 0%, 100% 38%, 82% 100%, 18% 100%, 0% 38%);
  }
  
  .hexagon {
	clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
  }
  
  // Animations
  @keyframes rotate {
	0% {
	  transform: rotate(0);
	}
	100% {
	  transform: rotate(360deg);
	}
  }
  
  @keyframes scale {
	0%, 100% {
	  transform: scale(1);
	}
	50% {
	  transform: scale(1.2);
	}
  }
  
  @keyframes skew {
	0%, 100% {
	  transform: skew(0);
	}
	50% {
	  transform: skew(20deg);
	}
  }
